
import {mapState, mapActions} from 'vuex'
import helper from '@/mixins/helper'
import tool from '@/utils/tool'
import Scroll from '@/components/Scroll'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'


export default {
name: "fans-list",
components: {
Tab,
TabItem,
Scroll
},
mixins: [helper],
data() {
return {
dataArr: [1,2,3],
currentTab: 0,
tabList: [
{
name: '',
count: 0
},
{
name: '',
count: 0
},
{
name: '',
count: 0
}
],
}
},
computed: {
...mapState({
list: state => state.fans.list,
nomore: state => state.fans.nomore,
})
},
watch: {},
mounted() {

},
methods: {
...mapActions({
getList: 'fans/getList',
loadMore: 'fans/loadMore'
}),

handlePullUp() {
setTimeout(()=>{
this.dataArr = [1,2,3]
},3000)
},

handlePulldown() {
setTimeout(()=>{
this.dataArr = [1,2,3]
},3000)
}
}
}



