import {mapState, mapActions} from 'vuex'

export default {
    computed: {},
    methods: {
        ...mapActions({
            showLoading: "global/showLoading",
            hideLoading: "global/hideLoading",
            showToast: "global/showToast"
        }),
        linkTo(url, query) {
            if (!url) return false
            if (/^http/.test(url)) {
                this.$router.push({
                    path: '/external-link',
                    query: {
                        url: url
                    }
                })
            } else {
                this.$router.push({
                    path: url,
                    query
                })
            }
        },
        replaceTo(url, query) {
            if (!url) return false
            if (/^http/.test(url)) {
                this.$router.replace({
                    path: '/external-link',
                    query: {
                        url: url
                    }
                })
            } else {
                this.$router.replace({
                    path: url,
                    query
                })
            }
        },
        alert(title, msg, cb) {
            var alert = {
                show: true,
                title: title,
                content: msg,
                onHide: () => {
                    cb && cb()
                }
            }
            this.$store.commit('global/updateAlert', {alert})
        }
    },
    data() {
        return {
            pullDownRefreshObj: {
                threshold: 50,
                stop: 40,
                txt: ''
            },
            pullUpLoadObj: {
                threshold: 50,
                txt: {
                    more: '',
                    noMore: '~'
                }
            },
        }
    }
}



